body {
    font-family: "Poppins";
  }
  table {
    width: 100%;
    /* text-align: center; */
    text-align: left;
    border-collapse: collapse;
    border-color: transparent;
  }
  
  th,
  td {
    padding: 10px 5px;
    /* border: none; */
  }
  thead tr:nth-child(1) {
    background-color: hsl(354, 100%, 60%);
    color: #fff;
    text-align: center;
  }
  thead tr:nth-child(2) {
    background-color: hsl(354, 100%, 70%);
    color: #fff;
  }
  tfoot tr:nth-child(2) {
    background-color: hsl(354, 100%, 60%);
    color: #fff;
    text-align: center;
  }
  tfoot tr:nth-child(1) {
    background-color: hsl(354, 100%, 70%);
    color: #fff;
  }
  thead {
    border: none;
  }
  thead tr,
  thead td {
    border: none;
  }
  .default-bg {
    background-color: #ffffff;
    
    &.depth-0 {
      background-color: #ffdafb;
    }
    
    &.depth-1 {
      background-color: #a8d1e7;
    }
    
    &.depth-2 {
      background-color: #8cf4a5;
    }
  
    &.depth-3 {
      background-color: #a6f6eb;
    }
  
    &.depth-4 {
      background-color: #ffd4cc;
    }
  
    &.depth-5 {
      background-color: #bfbbf3;
    }
  
    &.depth-6 {
      background-color: #fee5e0;
    }
  }
  
  button {
    background-color: transparent;
    padding: 0;
    /* width: 40px;
    height: 40px; */
    border: none;
    /* font-size: 130%; */
  }
  
  tfoot tr {
    border: 1px solid #fff;
  }
  