body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url('./asset/img/paris2.jpg');
  background-attachment: fixed;
}

body, html, #root{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-upload, .ant-upload-drag {
  margin-top: 3%;
}

.ant-upload-drag-container {
  padding-left: 10px;
  padding-right: 10px;
}

.ant-upload-list, .ant-upload-list-text{
  color: white;
  background-color: slategrey;
  border-radius: 8px;
  opacity: 0.7;
  margin-top: 3%;
}

#export-btn {
  background-color: aliceblue;
}

#export-btn[disabled] {
  background-color: gray;
  color: white;
  mix-blend-mode: initial;
}

#footer-area {
  position: fixed;
  width: 100%;
  bottom: 0;
}

footer {
  position: relative;
  height: 50px;
  width: 100%;
  background-color: #e3dcca;
}

p.copyright {
  position: absolute;
  width: 100%;
  color: #EB2F96;
  line-height: 20px;
  font-size: 1.1em;
  text-align: center;
  bottom: -3px;
}

span.email {
  color: #c78a72;
  font-style: italic;
  font-size: unset;
}

#result-display-btn {
  background-color: #ff93ce;
  opacity: 0.8;
  color: black;
  width: 100%;
}

#result-display-btn[disabled] {
  background-color: gray;
  opacity: 0.8;
  color: white;
  width: 100%;
}

.display-result-btn-area {
  margin-top: 0.3%;
}

.display-result-area {
  margin-top: 0.3%;
}

table#result-table {
  border-collapse: collapse;
  border: 3px solid black;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  background: aliceblue;
}

th, td {
  min-width: 3em;
  max-width: 50em;
  padding-left: 8px;
  padding-right: 8px;
  border-collapse: collapse;
}

th {
  border: 2px solid gray;
}

td {
  border: 1px solid gray;
  word-wrap: break-word;
}

.display-result-tbl-area {
  resize: vertical;
  overflow: auto;
  margin-bottom: 50px;
}

